					<!-- <navigator url="ceshi" class="ls_item">
						<image src="@/static/img/hy_tiwen_jilu_icon.png" mode="" style="width: 34rpx;height: 40rpx;"></image>
						<view class="hui_24 ">测试</view>
					</navigator> -->
