					</navigator>
				</view>
			</view>
		</view>
		<tabBar :currentPage="currentPage"  ref="mainindex"></tabBar>
	</view>
</template>

<script>
	import tabBar from '@/components/y_tabbar/tabbar.vue';
	
export default {
	created() {},
	onShow() {
		this.huiqu_login()
		this.huo_qu_is_yaoqing()
		uni.removeStorageSync('user_chat_list');
	},
	components: {

		tabBar
	},
	onLoad() {
		
	},
      onHide() {
		// #ifdef APP-PLUS
		uni.closeSocket();
		// #endif
		},
	data() {
		return {
			currentPage:'index/my',
			user:'',
			img_url: uni.getStorageSync('img_url'),
			is_login:false,
			geshu:'',
			baojia:'',
			yaoqing:'',
			is_yaoqing:2,
			is_tixian:2
			
		};
	},
	methods: {
		kaiqi() {
		console.log('kaiqi')
			let that = this;

			var url = that.$http.WebSocket_url;
		uni.connectSocket({
		    url: 'wss://' + url + ':3348',
			success:(data)=>{
				console.log("websocket连接成功",data);
			},
			fail:(err)=> {
			},
		    complete: (res)=> {
			
			}
		});
         uni.onSocketOpen(function (res) {
			  console.log('WebSocket连接已打开！',res);
			 
			});
			
			uni.onSocketError(function (res) {
			  console.log('WebSocket连接打开失败，请检查！');
			  
			});
		
		
		uni.onSocketMessage(function (res) {
			var data = JSON.parse(res.data);
			if (data.type == 'init') {
				console.log('init');
				console.log('client_id', data.client_id);
				uni.request({
					url: that.$http.baseUrl + '/push/gatewayworker/bind',
					method: 'POST',
					data: {
						client_id: data.client_id
					},
					success: function(resp) {
						console.log(resp, 'bind');
					},
					fail: function(resp) {}
				});
			
			} else if (data.type == 'say') {
				console.log('say');
				
				if (data.state) {
					// #ifdef APP-PLUS
					void plus.push.createMessage('用户端收到一条新消息');
					// #endif

					 that.$refs.mainindex.huoqunum();
				}
			} else {
				console.log('else');
			}
			
		});
		
		uni.onSocketClose(function (res) {
		  console.log('uniapp 已关闭！');
		});
		
		},
		qiehuan() {
					// #ifdef APP-PLUS
			var that=this
			// var url = that.$http.WebSocket_url;
			// socket.closeSocket({
			//   url: 'wss://' + url + ':3348',
			// });
			uni.closeSocket();

				// #endif
				
				
				uni.removeStorageSync('user_chat_list');
				
				
			uni.reLaunch({
				url: '../ls/my'
			});
			
			
			
		},
		huoqu_user(){
			// 获取用户信息
			this.$http
				.post({
					url: '/mapi/user/user'
				})
				.then(res => {
					this.user = res.data.user;
					
				});
		},
		huo_qu_is_yaoqing(){
			this.$http
				.post({
					url: '/mapi/index/openinvite'
				})
				.then(res => {
					
					this.is_yaoqing=res.data.openinvite
				});
		},
		go_yaoqing(){
			if(this.is_login){
				
				uni.navigateTo({
					url:'app_down?lianjie='+this.yaoqing
				})
				  
			
				
			}else{
				uni.navigateTo({
					url:'login'
				})
				return false
			}
			
		},
		huiqu_login(){
			
			this.$http
				.post({
					url: '/index/login/islogin'
				})
				.then(res => {
					if(res.data.user!=''){
					
						this.$refs.mainindex.huoqunum();
						this.is_login=true
						this.huoqu_user()
						this.huoqu_geshu()
						this.huoqu_baojia()
						this.huoqu_lianjie()
						this.huoqu_tixian()

						this.kaiqi();
						
					}else{
						this.is_login=false
					}
				});
		},
		huoqu_tixian(){
			this.$http
				.post({
					url: '/mapi/index/getuser_withdrawal'
				})
				.then(res => {
					this.is_tixian = res.data.user_withdrawal;
				});
		},
		huoqu_lianjie(){
			// 获取下载
	
				this.$http
					.post({
						url: '/mapi/user/yaoqing'
					})
					.then(res => {
						this.yaoqing = res.data.randcode1;
					});
		},
	
		huoqu_geshu(){
			this.$http
				.post({
					url: '/mapi/index/usercount'
				})
				.then(res => {
					
					this.geshu=res.data.count
				});
		},
		huoqu_baojia(){
			this.$http
				.post({
					url: '/mapi/index/getzixun'
				})
				.then(res => {
					
					this.baojia=res.data.zhan
				});
		},
		go_geren(){
			uni.navigateTo({
				url:'geren_xinxi'
			})
		}
	},
	
};
</script>

<style>
page {
	background-color: #f7f7f7;
}

.ls_tx {
	width: 110rpx;
	height: 110rpx;
