	width: 25%;
}

.jiedan_item image {
	width: 78rpx;
	height: 79rpx;
	margin-bottom: 20rpx;
}

.my_list {
	display: flex;
	/* justify-content: space-between; */
	flex-wrap: wrap;
}
.my_bottom {
	padding: 0 30rpx;
}
.ls_item {
	text-align: center;
	width: 33.33%;
	margin-bottom: 60rpx;
}

