}

.zhanghao_item {
	width: 33.33%;
	text-align: center;
	margin-bottom: 60rpx;
}

.zhanghao_guanli .zhanghao_item {
	margin-bottom: 37rpx;
}
.zhanghao_item image {
	margin-bottom: 16rpx;
}
.zhanghao_guanli .zhanghao_item image {
	margin-bottom: 8rpx;
}
.xianshi {
	height: 82rpx;
	position: relative;
	display: flex;
}
.jiage {
	position: absolute;
	right:-32rpx;
	top: 26%;
	text-align: center;
	line-height: 48rpx;
	transform: translate(-0%, -50%);
	width: 280rpx;
	height: 48rpx;
	background-color: #ffad2b;
	border-radius: 24rpx;
	transform: scale(0.7);
	

}
.xianshi image {
	width: 100%;
	height: 100%;
}
.zhanghao_guanli .my_title {
	margin-bottom: 50rpx;
}
.tuiguang_guanli {
	padding-bottom: 30rpx;
}
.my_top_weoienglu{
	display: flex;
	align-items: center;
	}

	.go_denglu{
		margin-right: 20rpx;
		border: 2rpx solid #EEEEEE;
		border-radius: 6rpx;
		width: 120rpx;
		height: 50rpx;
		line-height: 50rpx;
		text-align: center;
	}
	.go_zhuce{
		border-radius: 6rpx;
		width: 120rpx;
		height: 50rpx;
		line-height: 50rpx;
		border: 2rpx solid #EEEEEE;
		text-align: center;
	}
	.my_bottom{
		padding-bottom: 150rpx;
	}
</style>
